<template>
  <div>
      <label>告警级别：</label>
      <da-radio-select v-model="alarmLevel" :maxNum="1" :option-lists="alarmLevelList" label="name" value-key="id" @change="changealArmLevel"></da-radio-select>
      <label>告警类型：</label>
      <da-radio-select v-model="alarmCategory" :maxNum="1" :option-lists="alarmCategoryList" label="name" value-key="id" @change="changeAlarmCategory"></da-radio-select>
        <button type="button" @click="search">
          <span>查询</span>
        </button>
        <button type="button" @click="save">
          <span>保存</span>
        </button>
      <da-table class="analysis-table" :colums="configTableColumn"  :table-list="configTable">
        <el-table-column property="operation" label="接收方式" width="150" slot="operation" align="center" fixed="right">
          <template slot-scope="scope">
           <el-checkbox-group v-model="configTableC[scope.$index]" @change="cheeckChange(configTableC[scope.$index], scope.row)">
           <el-checkbox :label="1">弹窗</el-checkbox>
           <el-checkbox :label="2">短信</el-checkbox>
           <el-checkbox :label="3">邮件</el-checkbox>
           <el-checkbox :label="4">微信</el-checkbox> 
           </el-checkbox-group>
          </template>
        </el-table-column>
      </da-table>
  </div>
</template>

<script>
import qs from "qs";
import { cycleTime, calcTime, deepCopy } from "../../../util/util";
export default {
  data() {
    return {
      configTableC: [],
      alarmLevel: "", //告警的等级
      alarmLevelList: [
        { name: "全部", id: 0 },
        { name: "一类", id: 1 },
        { name: "二类", id: 2 },
        { name: "三类", id: 3 },
        { name: "四类", id: 4 }
      ], //告警的等级列表
      selectAlarmLevel: "", //选择的告警级别
      alarmCategory: "", //告警类型
      alarmCategoryList: [], //告警分组列表
      selectAlarmCategory: "", //选择的告警分组
      configTableColumn: [
        {
          label: "告警项",
          prop: "name"
        },
        {
          label: "告警级别",
          prop: "level",
          displace: { 1: "一类", 2: "二类", 3: "三类", 4: "四类" }
        },
        {
          label: "所属分组",
          prop: "alarmGroupId",
          displace: {}
        }
      ],
      configTable: [],
      userFocusList: [],
      alarmItem: [],
      userInfo: []
    };
  },
  methods: {
    //checkgroup数据处理
    cheeckChange(a, b) {
      b.receiveTypeArray = a;
    },
    loadData() {
      this.getCurrentUserID();
      this.getExceptionCountList();
      this.getConfigTable();
    },
    //获取告警分组
    getExceptionCountList() {
      this.$ajax
        .get("rest/analysisData/alarmGroup", {
          params: {
            title: "查询告警分组" //功能块标题，作为返回值的一部分返回，用于识别具体是哪一个功能块调用该URI
          }
        })
        .then(res => {
          if (res.data.status) {
            let columArray = {};
            this.$each(res.data.records, item => {
              columArray[item.id] = item.name;
            });
            this.configTableColumn[2].displace = columArray;
            let data = [];
            this.$each(res.data.records, item => {
              data.push({
                name: item.name,
                idValue: item.id,
                value: 0
              });
            });
            data = [{ name: "全部", idValue: "", value: 0 }].concat(data);
            this.$each(data, (item, i) => {
              this.alarmCategoryList.push({
                name: item.name,
                idValue: item.idValue,
                id: i
              });
            });
            this.selectAlarmCategory = this.alarmCategoryList[0].idValue;
          }
        });
    },
    //切换等级
    changealArmLevel() {
      switch (this.alarmLevel) {
        case 0:
          this.selectAlarmLevel = "";
          break;
        case 1:
          this.selectAlarmLevel = "1";
          break;
        case 2:
          this.selectAlarmLevel = "2";
          break;
        case 3:
          this.selectAlarmLevel = "3";
          break;
        case 4:
          this.selectAlarmLevel = "4";
          break;
      }
    },
    //切换数据
    changeAlarmCategory() {
      for (let i = 0; i < this.alarmCategoryList.length; i++) {
        if (this.alarmCategory == this.alarmCategoryList[i].id) {
          this.selectAlarmCategory = this.alarmCategoryList[i].idValue;
          break;
        }
      }
    },
    //搜索
    search() {
      this.getConfigTable();
    },
    //获得告警数据
    getConfigTable() {
      this.$ajax
        .get("rest/analysisData/alarmItem", {
          params: {
            title: "查询告警项", //功能块标题，作为返回值的一部分返回，用于识别具体是哪一个功能块调用该URI
            userIds: null, //用户ID，为空表示不作为查询条件，多个用逗号隔开，如"1,2,..."
            alarmGroups: this.selectAlarmCategory, //告警分组ID，为空表示不作为查询条件，多个用逗号隔开，如"1,2,..."
            alarmLevels: this.selectAlarmLevel,
            receiveTypes: null, //用户接收告警类型，1:弹窗；2：邮件；3：短信；4：微信，为空时表示不作为查询条件
            isAlarmGroup: true, //是否返回告警分组
            isReceiveType: true //是否返回用户接收告警类型
          }
        })
        .then(res => {
          if (res.data.status) {
            this.alarmItem = res.data.records;
            let tempArray = [];
            let idArray = [];
            for (let i = 0; i < res.data.records.length; i++) {
              let index = idArray.indexOf(res.data.records[i].id);
              if (index < 0) {
                tempArray.push(res.data.records[i]);
                tempArray[tempArray.length - 1]["receiveTypeArray"] = [];
                tempArray[tempArray.length - 1]["receiveTypeArray"].push(
                  res.data.records[i].receiveType
                );
                idArray.push(res.data.records[i].id);
              } else {
                tempArray[index]["receiveTypeArray"].push(
                  res.data.records[i].receiveType
                );
              }
            }

            this.configTable = tempArray;
            this.configTableC = tempArray.map(item => {
              return item.receiveTypeArray;
            });
          }
        });
    },
    //获得当前用户ID
    getCurrentUserID() {
      return this.$ajax({
        method: "get",
        url: "rest/user/currentUser",
        params: {}
      })
        .then(res => {
          if (res.data.status) {
            this.userInfo = res.data.records;
          }
        })
        .catch(err => {});
    },
    //点击保存按钮
    save() {
      this.configTable.forEach(item => {
        for (let i = 0; i < item.receiveTypeArray.length; i++) {
          let userFocus = { id: "", alarmItemId: "", userId: "", type: "" };
          userFocus.id = item.id;
          userFocus.alarmItemId = item.id;
          userFocus.userId = this.userInfo.id;
          userFocus.type = item.receiveTypeArray[i];
          this.userFocusList.push(userFocus);
        }
      });
      this.$ajax({
        url: "rest/alarmGroup/userFocus",
        method: "post",
        data: this.userFocusList
      }).then(res => {
        if (res.data.status) {
          alert("保存成功");
        } else {
          alert("操作失败");
        }
      });
    }
  },
  mounted() {
    this.loadData();
  }
};
</script>

<style scoped>
.alarmhistory-padb-10 {
  padding-bottom: 10px;
}

.alarmhistory-padb-10 > label {
  padding-left: 10px;
}
.alarmhistory-padl-10 {
  padding-left: 10px;
}
</style>



